

/* =============================================================
   MindSphere Design System colors colors
   ========================================================== */
    
/* Base colors colors */
    $base000: #000000;
    $base200: #323232;
    $base450: #595959;
    $base600: #969696;
    $base750: #BEBEBE;
    $base800: #D2D2D2;
    $base900: #F0F0F0;
    $base1000: #FFFFFF;
    
/* Primary color colors */
    $primary-darker: #354C80;
    $primary-dark: #005CBF;
    $primary: #006FE6;
    $primary-light: #009EFF;
    $primary-lighter: #7FB7F2;
    $primary-lightest: #CCE2FA;
    
/* Functional colors colors */
    $info-dark: #235461;
    $info: #006FE6;
    $info-light: #BBD0D7;
    $info-lighter: #D1E8F0;
    $warning-dark: #665E48;
    $warning: #FFC800;
    $warning-light: #E6DBB7;
    $warning-lighter: #FFEDB5;
    $error-dark: #811211;
    $error: #F62447;
    $error-light: #D6B4B4;
    $error-lighter: #FCD3D2;
    $success-dark: #5E6919;
    $success: #65C728;
    $success-light: #C8D1BA;
    $success-lighter: #E6EED1;
    
/* OS Bar / Launchpad colors colors */
    $firefly: #1E2832;
    $aquaHaze: #DCE1E6;
    


/* =============================================================
   Siemens Corporate colors colors
   ========================================================== */
    
/* Primary colors colors */
    $stoneDark: #3C464B;
    $stoneLight: #879BAA;
    $stoneLight35: #BECDD7;
    $sandDark: #73645A;
    $sandLight: #AAAA96;
    $sandLight35: #D7D7CD;
    $snow: #FFFFFF;
    
/* Accent colors colors */
    $accentTealDark: #00646E;
    $accentTealLight: #41AAAA;
    $accentBlueDark: #005F87;
    $accentBlueLight: #50BED7;
    $accentYellowDark: #EB780A;
    $accentYellowLight: #FFB900;
    $accentRedDark: #641946;
    $accentRedLight: #AF235F;
    $accentGreenDark: #647D2D;
    $accentGreenLight: #AAB414;
    


 /* =============================================================
    CSS variables for all colors
    ========================================================== */
:root {
    /* Base colors colors */
    --color-base000: 0,0,0;
    --color-base200: 50,50,50;
    --color-base450: 89,89,89;
    --color-base600: 150,150,150;
    --color-base750: 190,190,190;
    --color-base800: 210,210,210;
    --color-base900: 240,240,240;
    --color-base1000: 255,255,255;
       
    /* Primary color colors */
    --color-primary-darker: 53,76,128;
    --color-primary-dark: 0,92,191;
    --color-primary: 0,111,230;
    --color-primary-light: 0,158,255;
    --color-primary-lighter: 127,183,242;
    --color-primary-lightest: 204,226,250;
       
    /* Functional colors colors */
    --color-info-dark: 35,84,97;
    --color-info: 0,111,230;
    --color-info-light: 187,208,215;
    --color-info-lighter: 209,232,240;
    --color-warning-dark: 102,94,72;
    --color-warning: 255,200,0;
    --color-warning-light: 230,219,183;
    --color-warning-lighter: 255,237,181;
    --color-error-dark: 129,18,17;
    --color-error: 246,36,71;
    --color-error-light: 214,180,180;
    --color-error-lighter: 252,211,210;
    --color-success-dark: 94,105,25;
    --color-success: 101,199,40;
    --color-success-light: 200,209,186;
    --color-success-lighter: 230,238,209;
       
    /* OS Bar / Launchpad colors colors */
    --color-firefly: 30,40,50;
    --color-aquaHaze: 220,225,230;
       
    /* Primary colors colors */
    --color-stoneDark: 60,70,75;
    --color-stoneLight: 135,155,170;
    --color-stoneLight35: 190,205,215;
    --color-sandDark: 115,100,90;
    --color-sandLight: 170,170,150;
    --color-sandLight35: 215,215,205;
    --color-snow: 255,255,255;
       
    /* Accent colors colors */
    --color-accentTealDark: 0,100,110;
    --color-accentTealLight: 65,170,170;
    --color-accentBlueDark: 0,95,135;
    --color-accentBlueLight: 80,190,215;
    --color-accentYellowDark: 235,120,10;
    --color-accentYellowLight: 255,185,0;
    --color-accentRedDark: 100,25,70;
    --color-accentRedLight: 175,35,95;
    --color-accentGreenDark: 100,125,45;
    --color-accentGreenLight: 170,180,20;
       

    --color-info: var(--color-primary); // meta color for informational messages and notifications
    --color-shadow: var(--color-base000); // (box) shadows color
    --color-overlay: var(--color-base200); // overlay background color
    --color-interface: var(--color-base1000); // main interface color
    --color-interface-secondary: var(--color-base900); // leading and context region
    --color-interface-highlight: var(--color-base1000); // header sections, card, container etc
    --color-border: var(--color-base750);
    --color-font: var(--color-base200); // color for main font (headlines and text)
    --color-font-secondary: var(--color-base600); // color for secondary font objects, e.g. descriptions and subheadlines
    --color-font-disabled: var(--color-base600); // color for disabled elements' font / labels
}
