.form {
    &__formGroup.has-error {
        margin-bottom: 16px;
        margin-bottom: rem(16px);

        &:before {
            background-color: $error;
            background-color: rgba(var(--color-error), 1);
            content: '';
            display: block;
            bottom: 0;
            left: -10px;
            position: absolute;
            top: 0;
            width: 2px;
        }

        > .inputGroup.is-invalid {
            > .checkboxWrapper > label,
            > .selectWrapper > select,
            > .radioButtonWrapper > label,
            > input,
            > textarea {
                border-color: $error;
                border-color: rgba(var(--color-error), 1);
                color: $error;
                color: rgba(var(--color-error), 1);

                &::-webkit-input-placeholder {
                    color: $error;
                    color: rgba(var(--color-error), 1);
                }
                &::-moz-placeholder {
                    color: $error;
                    color: rgba(var(--color-error), 1);
                }
                &:-ms-input-placeholder {
                    color: $error;
                    color: rgba(var(--color-error), 1);
                }

                &[class$="__description"] {
                    color: $base600;
                    color: rgba(var(--color-font-secondary), 1);
                }
            }

            .selectWrapper > select {
                // remove dotted extra outline on firefox browser focus
                &:-moz-focusring {
                    color: transparent;
                    text-shadow: 0 0 0 $error;
                    text-shadow: 0 0 0 rgba(var(--color-error), 1);
                }
            }
        }
    }

    &__requiredMsg, .inputGroup--error {
        color: $error;
        color: rgba(var(--color-error), 1);
        font-family: $defaultFont;
        font-size: 13px;
        font-size: rem(13px);
        line-height: 17px;
        line-height: rem(17px);
        margin-top: 2px;
        margin-top: rem(2px);
    }

    .inputGroup--error {
        cursor: pointer;
        @include iconMdsp-bold(goTo, after);

        &:after {
            color: $error;
            color: rgba(var(--color-error), 1);
            font-size: 16px;
            font-size: rem(16px);
            line-height: 13px;
            line-height: rem(13px);
        }

        // inserts a line break before every >1th validation error label
        &:nth-child(n+2):before {
            content: "\A";
            white-space: pre;
        }
    }

    &__buttons {
        margin-top: 8px;
        margin-top: rem(8px);
    }

    .messageWrapper {
        padding: 12px 0 24px 0;
        padding: rem(12px 0 24px 0);
    }
}
